@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

html {
    overflow: hidden;
}
body {
    margin: 0;
    overflow-x: hidden;
    background: linear-gradient(90deg, #5F5385 0%, #B77791 100%);
}

h1, h2, h3, h4, h5, h6, div, input, textarea, p, a {
    font-family: 'Montserrat', sans-serif;
}

input::placeholder {
    font-family: 'Montserrat', sans-serif;
}
